@import url('../../styles/global.scss');
.ChatBox-container {
    .chat-header {
        padding: 1rem 1rem 0rem 1rem;
        display: flex;
        align-items: center;
        
    }
    
    .chat-body {
        display: flex;
        flex-direction: column;
        padding: 1.5rem;
        overflow-x: hidden;
        max-height: 412px;
        
    
        .message {
            margin: 1px 0;
            background-color: var(--indigo);
            color: white;
            padding: 0.7rem;
            border-radius: 1rem 1rem 1rem 0;
            max-width: 48rem;
            width: fit-content;
            display: flex;
            flex-direction: column;
            gap: 0.5rem;
          }
          
          .message>span:nth-child(2){
              font-size: 0.7rem;
              color: var(--textColor);
              align-self: end;
    
          }
          
          .own {
            align-self: flex-end;
            border-radius: 1rem 1rem 0 1rem;
            background: linear-gradient(98.63deg, #24e4f0 0%, #358ff9 100%);
          }
    }
    
    
    
    
    
    .chat-sender {
        background: white;
        display: flex;
        justify-content: space-between;
        height: 3.5rem;
        align-items: center;
        gap: 1rem;
        padding: 0.8rem;
        border-radius: 1rem;
        align-self: end;
    }
    .chat-sender>input{
        height: 70%;
        background-color: rgb(236, 236, 236);
        border-radius: 0.5rem;
        border: none;
        outline: none;
        flex  :1 ;
        font-size: 14px;
        padding: 0px 15px 0px 15px;
    }
    .chat-sender>div:nth-of-type(1)
    {
        background: rgb(233, 233, 233);
        border-radius: 0.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        cursor: pointer
    }
    .chat-sender>div{
        height: 70%;
        padding: 0px 15px 0px 15px;
    }
}

.tab-chat {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

